.slippage-modal-overlay {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.25);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slippage-modal {
  background: var(--card-bg, #fff);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  padding: 1.5rem 1.2rem 1.2rem 1.2rem;
  min-width: 260px;
  max-width: 95vw;
  border: 1.5px solid var(--card-border, #e5e7eb);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slippage-modal .slippage-options {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.75rem;
  margin-bottom: 1.2rem;
}

.slippage-modal .slippage-option {
  padding: 0.7rem 0;
  border: 1.5px solid var(--card-border, #e5e7eb);
  background: var(--button-secondary-bg, #f8fafc);
  color: var(--text-primary, #1e293b);
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, border 0.2s, color 0.2s;
}
.slippage-modal .slippage-option.active,
.slippage-modal .slippage-option:hover {
  background: var(--accent-blue, #2563eb);
  color: #fff;
  border-color: var(--accent-blue, #2563eb);
}

.slippage-modal .custom-slippage {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.slippage-modal .custom-slippage-input {
  display: flex;
  align-items: center;
  background: var(--input-bg, #e5e7eb);
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
}

.slippage-modal .custom-slippage-input input {
  background: transparent;
  border: none;
  color: var(--text-primary, #1e293b);
  font-size: 1.1rem;
  width: 60px;
  text-align: right;
  outline: none;
}

.slippage-modal .custom-slippage-input span {
  margin-left: 0.5rem;
  color: var(--text-tertiary, #94a3b8);
}

.slippage-modal .confirm-slippage {
  padding: 0.7rem 0;
  border-radius: 8px;
  border: none;
  background: var(--accent-blue, #2563eb);
  color: #fff;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  margin-top: 0.5rem;
  transition: background 0.2s;
}
.slippage-modal .confirm-slippage:hover {
  background: var(--accent-green, #00c853);
}

.tab-navigation {
  background: var(--card-bg, #fff);
  border-radius: 14px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.10);
  padding: 0.5rem 0.25rem;
  margin-bottom: 1.2rem;
}
.tab-navigation button {
  background: none;
  border: none;
  border-radius: 10px;
  margin: 0 0.15rem;
  padding: 0.7rem 0.2rem;
  font-weight: 500;
  transition: background 0.2s, color 0.2s;
}
.tab-navigation button.active, .tab-navigation button:focus, .tab-navigation button:hover {
  background: var(--accent-blue, #2563eb);
  color: #fff;
}

.chart-section, .token-details, .notification-bar, .metric-card {
  background: var(--card-bg, #fff);
  border-radius: 14px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.10);
  padding: 1.2rem 1rem;
  margin-bottom: 1.2rem;
}

.metric-card {
  margin-bottom: 0.7rem;
  min-width: 120px;
}

.holders-panel, .chat-panel, .transactions-panel, .swap-panel {
  background: var(--card-bg, #fff);
  border-radius: 14px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.10);
  padding: 1.2rem 1rem;
  margin-bottom: 1.2rem;
}