.trending-tokens-container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  min-height: 100vh;
  background: transparent;
}

.trending-header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  padding: 20px 0;
}

.trending-title {
  display: flex;
  align-items: center;
  gap: 12px;
}

.trending-icon {
  font-size: 2rem;
  color: #8b5cf6;
}

.trending-title h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
  font-family: 'Space Grotesk', sans-serif;
}

.tab-navigation {
  display: flex;
  gap: 12px;
  margin-bottom: 30px;
  padding: 0 10px;
}

.tab-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 8px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 500;
  font-size: 0.95rem;
}

.tab-btn:hover {
  background: var(--card-hover-bg);
  color: var(--text-primary);
}

.tab-btn.active {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.5);
  color: #8b5cf6;
}

.tab-icon {
  font-size: 1.1rem;
}

.token-cards-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: clamp(16px, 2vw, 32px);
  margin-bottom: 40px;
  width: 100%;
}

.token-card-wrapper {
  cursor: pointer;
  transition: transform 0.2s ease;
  min-width: 0;
  width: 100%;
}

.token-card-wrapper:hover {
  transform: translateY(-2px);
}

.pagination-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 40px;
  padding: 20px 0;
}

.pagination-btn {
  padding: 12px 24px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 500;
}

.pagination-btn:hover:not(:disabled) {
  background: rgba(127, 90, 240, 0.2);
  border-color: rgba(127, 90, 240, 0.5);
  transform: translateY(-1px);
}

.pagination-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pagination-info {
  color: #a8a8a8;
  font-size: 0.9rem;
  font-family: 'Space Grotesk', sans-serif;
}

/* Responsive Design - Liquid Layout */
/* Large Desktop (1400px and above) - 4 cards per row */
@media (min-width: 1400px) {
  .trending-tokens-container {
    padding: clamp(20px, 3vw, 40px);
  }
  
  .token-cards-grid {
    grid-template-columns: repeat(4, 1fr);
    gap: clamp(24px, 3vw, 48px);
  }
}

/* Medium Desktop (1024px to 1399px) - 4 cards per row */
@media (min-width: 1024px) and (max-width: 1399px) {
  .trending-tokens-container {
    padding: clamp(16px, 2.5vw, 32px);
  }
  
  .token-cards-grid {
    grid-template-columns: repeat(4, 1fr);
    gap: clamp(20px, 2.5vw, 36px);
  }
}

/* Tablet (768px to 1023px) - 2 cards per row */
@media (min-width: 768px) and (max-width: 1023px) {
  .trending-tokens-container {
    padding: clamp(12px, 2vw, 24px);
  }
  
  .trending-header {
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
  }
  
  .trending-title h1 {
    font-size: 2rem;
  }
  
  .tab-navigation {
    flex-wrap: wrap;
    gap: 8px;
  }
  
  .tab-btn {
    padding: 10px 16px;
    font-size: 0.9rem;
  }
  
  .token-cards-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: clamp(16px, 2vw, 28px);
  }
  
  .pagination-container {
    flex-direction: row;
    gap: 15px;
  }
  
  .pagination-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

/* Mobile (480px to 767px) - 1 card per row */
@media (min-width: 480px) and (max-width: 767px) {
  .trending-tokens-container {
    padding: clamp(10px, 1.5vw, 20px);
  }
  
  .trending-header {
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
  }
  
  .trending-title h1 {
    font-size: 1.75rem;
  }
  
  .tab-navigation {
    flex-wrap: wrap;
    gap: 6px;
  }
  
  .tab-btn {
    padding: 8px 14px;
    font-size: 0.85rem;
  }
  
  .token-cards-grid {
    grid-template-columns: 1fr;
    gap: clamp(12px, 1.5vw, 20px);
  }
  
  .pagination-container {
    flex-direction: column;
    gap: 15px;
  }
  
  .pagination-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

/* Small Mobile (below 480px) - 1 card per row */
@media (max-width: 479px) {
  .trending-tokens-container {
    padding: clamp(8px, 1vw, 16px);
  }
  
  .trending-header {
    flex-direction: column;
    gap: 12px;
    align-items: flex-start;
  }
  
  .trending-title h1 {
    font-size: 1.5rem;
  }
  
  .tab-navigation {
    flex-wrap: wrap;
    gap: 4px;
  }
  
  .tab-btn {
    padding: 6px 12px;
    font-size: 0.8rem;
  }
  
  .token-cards-grid {
    grid-template-columns: 1fr;
    gap: clamp(12px, 1vw, 16px);
  }
  
  .pagination-container {
    flex-direction: column;
    gap: 12px;
  }
  
  .pagination-btn {
    padding: 8px 16px;
    font-size: 0.85rem;
  }
} 