:root {
  /* Base variables */
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 24px;
  --transition-fast: 0.15s ease;
  --transition-normal: 0.25s ease;
  --transition-slow: 0.4s ease;
  
  /* Glassmorphism variables */
  --glass-bg: rgba(255, 255, 255, 0.08);
  --glass-border: rgba(255, 255, 255, 0.18);
  --glass-backdrop: blur(12px);
  --glass-shadow: 0 8px 32px rgba(0, 0, 0, 0.18);
  
  /* Color system */
  --text-primary: #ffffff;
  --text-secondary: #e2e2e2;
  --text-tertiary: #a8a8a8;
  --bg-primary: #0a0a0c;
  --bg-secondary: #111216;
  --bg-tertiary: #1a1b20;
  --card-bg: var(--glass-bg);
  --card-border: var(--glass-border);
  --accent-1: #7f5af0;
  --accent-2: #2cb67d;
  --accent-3: #f55050;
  --accent-4: #fca311;
  --accent-gradient: linear-gradient(135deg, var(--accent-1), #4361ee);
  --success: #2cb67d;
  --warning: #fca311;
  --danger: #f55050;
  --info: #4361ee;
  
  /* Typography */
  --font-primary: 'Space Grotesk', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  --font-secondary: 'Figtree', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  --font-size-xs: 0.65rem;
  --font-size-sm: 0.8125rem;
  --font-size-base: 0.9375rem;
  --font-size-md: 1.125rem;
  --font-size-lg: 1.3125rem;
  --font-size-xl: 1.625rem;
  --font-size-2xl: 2.125rem;
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
}

.light-mode {
  --glass-bg: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(0, 0, 0, 0.08);
  --glass-backdrop: blur(0px);
  --glass-shadow: 0 4px 12px rgba(0, 0, 0, 0.08);
  
  --text-primary: #121212;
  --text-secondary: #4a4a55;
  --text-tertiary: #7a7a85;
  --bg-primary: #f8f9fc;
  --bg-secondary: #f0f2f5;
  --bg-tertiary: #e6e9ef;
  --card-bg: var(--glass-bg);
  --card-border: var(--glass-border);
  --accent-1: #6c47c1;
  --accent-2: #1f9e6a;
  --accent-3: #e03131;
  --accent-4: #e58e26;
  --accent-gradient: linear-gradient(135deg, var(--accent-1), #3a56d4);
}

/* Main Token Card */
.main-token-card {
  background: var(--card-bg);
  backdrop-filter: var(--glass-backdrop);
  -webkit-backdrop-filter: var(--glass-backdrop);
  border: 1px solid var(--card-border);
  border-radius: var(--radius-lg);
  overflow: hidden;
  transition: var(--transition-normal);
  height: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
  font-family: var(--font-primary);
  min-height: clamp(280px, 25vh, 400px);
}

.main-token-card:hover {
  transform: translateY(-3px);
  border-color: rgba(127, 90, 240, 0.5);
}

.token-header {
  padding: clamp(12px, 2vw, 20px);
  display: flex;
  align-items: center;
  gap: clamp(8px, 1.5vw, 16px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.token-logo-lg {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  position: relative;
  overflow: hidden;
}

.token-logo-lg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
}

.token-logo-text {
  font-family: var(--font-primary);
  font-size: 1.2rem;
  font-weight: var(--font-weight-bold);
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.token-info {
  flex: 1;
}

.token-name {
  font-size: var(--font-size-md);
  font-weight: var(--font-weight-bold);
  margin-bottom: 4px;
  color: var(--text-primary);
}

.token-symbol {
  font-size: var(--font-size-sm);
  color: var(--text-tertiary);
  margin-bottom: 4px;
}

.contract-address {
  font-size: var(--font-size-xs);
  color: var(--text-tertiary);
  font-family: monospace;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.status-badge {
  padding: 4px 8px;
  border-radius: 6px;
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  display: flex;
  align-items: center;
  gap: 4px;
  background: rgba(255, 255, 255, 0.1);
  color: var(--text-secondary);
}

.status-badge svg {
  width: 12px;
  height: 12px;
}

.status-badge.migrated {
  background: rgba(44, 182, 125, 0.15);
  color: var(--success);
}

.status-badge.bonding {
  background: rgba(251, 191, 36, 0.15);
  color: var(--warning);
}

.status-badge.new {
  background: rgba(127, 90, 240, 0.15);
  color: var(--accent-1);
}

.status-badge.upcoming {
  background: rgba(67, 97, 238, 0.15);
  color: var(--info);
}

.token-body {
  padding: 16px;
  flex: 1;
}

.stats-container {
  background: rgba(255, 255, 255, 0.05);
  border-radius: var(--radius-md);
  padding: 12px;
  margin-bottom: 16px;
}

.stat-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
}

.stat-row svg {
  width: 14px;
  height: 14px;
  margin-right: 6px;
  color: var(--text-tertiary);
}

.stat-row span:first-child {
  display: flex;
  align-items: center;
}

.stat-value {
  color: var(--text-primary);
  font-weight: var(--font-weight-semibold);
  display: flex;
  align-items: center;
  gap: 4px;
}

.progress-container {
  margin-top: 16px;
  border-radius: 10px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.05);
  height: 8px;
}

.progress-fill {
  height: 100%;
  background: var(--accent-gradient);
  border-radius: 10px;
  position: relative;
  background-size: 200% 100%;
  animation: shimmer 2.5s infinite;
}

.progress-label {
  display: flex;
  justify-content: space-between;
  font-size: var(--font-size-xs);
  margin-top: 8px;
  color: var(--text-tertiary);
}

.token-footer {
  padding: 0 16px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}

.social-links {
  display: flex;
  gap: 10px;
}

.social-btn {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-tertiary);
  cursor: pointer;
  transition: var(--transition-fast);
  font-size: 0.85rem;
  text-decoration: none;
}

.social-icon {
  width: 32px;
  height: 32px;
  object-fit: contain;
  filter: brightness(0.8);
  transition: var(--transition-fast);
}

.social-btn:hover .social-icon {
  filter: brightness(1);
}

.social-btn:hover {
  background: var(--accent-1);
  color: white;
}

.swap-btn {
  flex: 1;
  padding: 10px 16px;
  background: var(--accent-gradient);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: var(--font-weight-semibold);
  cursor: pointer;
  transition: var(--transition-normal);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  font-family: var(--font-primary);
}

.swap-btn svg {
  width: 14px;
  height: 14px;
}

.swap-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(127, 90, 240, 0.35);
}

.like-btn {
  width: 36px;
  height: 36px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  border: none;
  color: var(--text-tertiary);
  cursor: pointer;
  transition: var(--transition-fast);
}

.like-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: var(--danger);
}

.like-btn.liked {
  color: var(--danger);
  animation: heartbeat 0.5s;
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

@keyframes heartbeat {
  0% { transform: scale(1); }
  10% { transform: scale(1.2); }
  20% { transform: scale(1); }
  30% { transform: scale(1.2); }
  40% { transform: scale(1); }
}

/* Responsive Design */
@media (max-width: 768px) {
  .main-token-card {
    max-width: 100%;
    min-height: clamp(260px, 30vh, 320px);
  }
  
  .token-header {
    padding: clamp(10px, 2vw, 16px);
  }
  
  .token-body {
    padding: clamp(10px, 2vw, 16px);
  }
  
  .token-footer {
    padding: 0 clamp(10px, 2vw, 16px) clamp(10px, 2vw, 16px);
  }
} 

@media (min-width: 769px) {
  .main-token-card {
    max-width: none;
  }
}

/* Fluid grid container for token cards */
.token-grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: clamp(16px, 2vw, 24px);
  width: 100%;
  padding: clamp(12px, 2vw, 20px);
  margin: 0 auto;
}

/* Adjust card size based on screen width */
@media (max-width: 480px) {
  .token-grid-container {
    grid-template-columns: 1fr;
    gap: 16px;
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  .token-grid-container {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .token-grid-container {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
}

@media (min-width: 1025px) and (max-width: 1400px) {
  .token-grid-container {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}

@media (min-width: 1401px) {
  .token-grid-container {
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: clamp(20px, 2vw, 32px);
  }
}